
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#price .deviceid {
  margin-top: 20px;
  background: #fff;
  padding: 15px 20px;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
}
#price .deviceid .fl {
  margin-right: 20px;
}
#price .deviceid .mk {
  color: #999;
}
#price .price {
  overflow: hidden;
  background: #fff;
  padding: 15px 20px;
}
#price .price .fl {
  margin-right: 20px;
}
#price .price .mk {
  color: #999;
}
#price .price .mk input {
  color: #999;
  width: 20px;
  border: none;
}
#price .price .mk input:focus {
  outline: none;
}
#price .smit {
  margin-top: 20px;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  background: #0f0;
  color: #fff;
  border-radius: 10px;
  text-align: center;
  padding: 20px 0;
}



